<template>
	<!-- 会议详情页面 -->
	<view class="page" v-if="checkPermission(['ROOT', 'MEETING:INSERT', 'MEETING:UPDATE'])">
		<!-- 会议标题部分 start -->
		<view class="header">
			<input type="text" class="title" v-model="title" placeholder="输入会议标题" placeholder-class="title-placeholder" />
			<image src="../../static/icon-18.png" mode="widthFix" class="edit-icon"></image>
		</view>
		<!-- 会议标题部分 end -->

		<!-- 会议详情信息填写 start -->
		<view class="attr">
			<view class="list">
				<view class="item">
					<view class="key">日期</view>
					<picker v-if="canEdit" mode="date" :value="date" @change="dateChange">
						<view class="uni-input">{{ date }}</view>
					</picker>
					<text v-if="!canEdit" class="value">{{ date }}</text>
				</view>
				<view class="item">
					<view class="key">开始时间</view>
					<picker v-if="canEdit" mode="time" :value="start" @change="startChange">
						<view class="uni-input">{{ start }}</view>
					</picker>
					<text v-if="!canEdit" class="value">{{ start }}</text>
				</view>
				<view class="item">
					<view class="key">结束时间</view>
					<picker v-if="canEdit" mode="time" :value="end" @change="endChange">
						<view class="uni-input">{{ end }}</view>
					</picker>
					<text v-if="!canEdit" class="value">{{ end }}</text>
				</view>
				<view class="item">
					<view class="key">会议类型</view>
					<picker v-if="canEdit" :value="typeIndex" :range="typeArray" @change="typeChange">{{ typeArray[typeIndex] }}</picker>
					<text v-if="!canEdit" class="value">{{ typeArray[typeIndex] }}</text>
				</view>
				<view class="item" v-if="typeArray[typeIndex] == '线下会议'" @tap="editPlace">
					<view class="key">地点</view>
					<view class="value">{{ place }}</view>
				</view>
			</view>
			<view @tap="editDesc">
				<text class="desc">{{ desc }}</text>
			</view>
		</view>
		<!-- 会议详情信息填写 end -->

		<!-- 添加参会者 start -->
		<view class="members">
			<view class="number">参会者（{{ members.length }}人）</view>
			<view class="member">
				<view class="user" v-for="one in members" :key="one.id" @longpress="deleteMember(one.id)">
					<image :src="one.photo" mode="widthFix" class="photo"></image>
					<text class="name">{{ one.name }}</text>
				</view>
				<view class="add"><image src="../../static/icon-19.png" mode="widthFix" class="add-btn" @tap="toMembersPage()"></image></view>
			</view>
		</view>
		<!-- 添加参会者 end -->

		<!-- 保存按钮部分 start -->
		<button class="btn" @tap="save">保存</button>
		<uni-popup ref="popupPlace" type="dialog">
			<uni-popup-dialog mode="input" title="编辑文字内容" placeholder="输入会议地点" :value="place" @confirm="finishPlace"></uni-popup-dialog>
		</uni-popup>
		<uni-popup ref="popupDesc" type="dialog">
			<uni-popup-dialog mode="input" title="编辑文字内容" placeholder="输入会议内容" :value="desc" @confirm="finishDesc"></uni-popup-dialog>
		</uni-popup>
		<!-- 保存按钮部分 end -->
	</view>
</template>

<script>
import uniPopup from '@/components/uni-popup/uni-popup.vue';
import uniPopupMessage from '@/components/uni-popup/uni-popup-message.vue';
import uniPopupDialog from '@/components/uni-popup/uni-popup-dialog.vue';
import utils from '@/utils/utils.js';
export default {
	components: {
		uniPopup,
		uniPopupMessage,
		uniPopupDialog
	},
	data() {
		return {
			opt: null, // 判断当前页面是创建会议还是查看会议详情的页面
			id: null,
			uuid: null,
			canEdit: true,
			title: '',
			date: '',
			start: '',
			end: '',
			typeArray: ['线上会议', '线下会议'],
			typeIndex: 0,
			place: '',
			desc: '会议内容',
			members: [],
			instanceId: null
		};
	},
	onShow: function() {
		let that = this;
		let pages = getCurrentPages();
		let currPage = pages[pages.length - 1];
		// 判断是否进入了成员选择界面，如果进入了就把添加的成员加入
		if (!currPage.hasOwnProperty('finishMembers') || !currPage.finishMembers) {
			console.log(currPage.finishMembers);
			if (that.opt == 'insert') {
				let date = new Date();
				date.setTime(date.getTime() + 30 * 60 * 1000); // 开始时间默认是半个小时后
				that.date = date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate();
				that.start = date.getHours() + ':' + date.getMinutes();
				date.setTime(date.getTime() + 60 * 60 * 1000); // 结束时间默认是一个小时后
				that.end = date.getHours() + ':' + date.getMinutes();
			} else if (that.opt == 'edit') {
				// 查询会议详细信息通过id
				that.ajax(that.url.searchMeetingById, 'POST', { id: that.id }, function(resp) {
					let result = resp.data.result;
					that.uuid = result.uuid;
					that.title = result.title;
					that.date = result.date;
					that.start = result.start;
					that.end = result.end;
					that.typeIndex = result.type - 1;
					that.place = result.place;
					let desc = result.desc;
					if (desc != null && desc != '') {
						that.desc = desc;
					}
					that.members = result.members;
					that.instanceId = result.instanceId;
				});
			}
		} else {
			console.log(currPage.finishMembers);
			let members = [];
			for (let one of currPage.members) {
				members.push(Number(one));
			}

			// 查询成员的详细信息
			that.ajax(that.url.searchMembers, 'POST', { members: JSON.stringify(members) }, function(resp) {
				let result = resp.data.result;
				that.members = result;
			});
		}
	},
	onLoad: function(options) {
		this.id = options.id;
		this.opt = options.opt;
	},
	methods: {
		// 转去成员页面
		toMembersPage: function() {
			let array = [];
			for (let one of this.members) {
				array.push(one.id);
			}
			uni.navigateTo({
				url: '../members/members?members=' + array.join(',')
			});
		},

		// 日期改变函数
		dateChange: function(e) {
			this.date = e.detail.value;
		},
		startChange: function(e) {
			this.start = e.detail.value;
		},
		endChange: function(e) {
			this.end = e.detail.value;
		},

		// 会议类型改变的函数
		typeChange: function(e) {
			this.typeIndex = e.detail.value;
		},

		// 线下会议内容
		editPlace: function() {
			if (!this.canEdit) {
				return;
			}
			this.$refs.popupPlace.open();
		},
		finishPlace: function(done, value) {
			if (value != null && value != '') {
				this.place = value;
				done();
			} else {
				uni.showToast({
					icon: 'none',
					title: '地点不能为空'
				});
			}
		},
		editDesc: function() {
			if (!this.canEdit) {
				return;
			}
			this.$refs.popupDesc.open();
		},
		finishDesc: function(done, value) {
			if (value != null && value != '') {
				this.desc = value;
				done();
			} else {
				uni.showToast({
					icon: 'none',
					title: '内容不能为空'
				});
			}
		},

		// 提交的数据是否为空或者时间是否错误
		contentValid: function(array) {
			let that = this;
			let res = '数据无问题';

			if (that.typeIndex == 1 && that.palce != "") {
				res = '线下会议地址不能为空'+that.typeIndex;
				return res;
			}
			if (that.title == "" || that.desc == "") {
				res = '会议内容不能为空';
				return res;
			}
			if (that.end < that.start) {
				res = '会议结束时间不能小于会议开始时间';
				return res;
			}
			if (!(array.length > 0)) {
				res = '未选择参会人员';
				return res;
			}
			return res;
		},

		save: function() {
			let that = this
			let array = [];
			for (let one of that.members) {
				array.push(one.id);
			}
			let res = that.contentValid(array);
			if (res != '数据无问题') {
				uni.showModal({
					title: '提示信息',
					content: res,
					success: function(res) {}
				});
				return;
			}
			let data = {
				title: that.title,
				date: that.date,
				start: that.start,
				end: that.end,
				type: Number(that.typeIndex) + 1,
				members: JSON.stringify(array),
				desc: that.desc,
				id: that.id,
				instanceId: that.instanceId
			};
			if (that.typeIndex == '1') {
				data.place = that.place;
			}
			let url;
			if (that.opt == 'insert') {
				url = that.url.insertMeeting;
			} else if (that.opt == 'edit') {
				url = that.url.updateMeetingInfo;
			}
			that.ajax(url, 'POST', data, function(resp) {
				console.log(resp)
				uni.showToast({
					icon: 'success',
					title: '保存成功',
					complete: function() {
						setTimeout(function() {
							uni.navigateBack({});
						}, 2000);
					}
				});
			});
		},

		// 删除已有的成员名单
		deleteMember: function(id) {
			let that = this;
			uni.vibrateShort({});
			uni.showModal({
				title: '提示信息',
				content: '删除该名参会人员？',
				success: function(resp) {
					let postion; // 标记需要删除的人员的id
					if (resp.confirm) {
						for (let i = 0; i < that.members.length; i++) {
							let one = that.members[i];
							if (one.id == id) {
								postion = i;
								break;
							}
						}
						that.members.splice(postion, 1);
					} else if (resp.cancel) {
					}
				}
			});
		},

		// 检验权限
		checkPermission: function(need_permission) {
			var user_permission = uni.getStorageSync('permission');
			return utils.checkPermission(user_permission, need_permission);
		}
	}
};
</script>

<style lang="less">
@import url('meeting.less');
</style>
